@import url(https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-regular-webfont.woff);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background-color: rgb(236, 236, 236);
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.main {
  margin: 20px 40px;
  background: #ffffff;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  border-radius: 20px;

}
.main_heading {
  border-radius: 20px 20px 0 0;
  position: relative;
  top: 0;
  background-color: #c7c7c7;
  background: linear-gradient(#c7c7c7 0%, #8f8e8e 100%);
}
.main_heading_name {
  text-align: center;
  padding: 10px;
  font-size: 30px;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  margin-bottom: 0;;
}

.preloader{
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  height: 100vh;
}
.preloader_img{
  margin:auto
}

.cards_container{
    display:flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    flex-wrap: wrap;
}
.content_heading{
    position: relative;
  
    background: linear-gradient(#B1B1B1 0%, #9c9c9c 100%);
    margin:0;
    height: 30px;
}
.content_heading_button{cursor: pointer;height: 20px; margin:5px; position: absolute;left:0;top:0;}
.content_heading_name{
    margin:auto;
    font-size: 20px;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    text-align: center;
    padding-top:5px;
}
.card{
    width:200px;
    min-height: 200px;
    background-color: bisque;
    margin:20px;
    background: #ececec;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.35);
    display:flex;
    flex-direction: column;  
    align-items: center;  
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.card p{
    display:block;
    text-align: center;
}
.card_author{
    margin:auto;
}
.card_title{
    margin:5px;
}
.card_img{ 
    height: 200px;
    width:200px;
    background-size: cover;
    cursor: pointer;
}
.albums_cards_container .card_img{-webkit-filter: opacity(70%);filter: opacity(70%);transition:0.5s;}
.albums_cards_container .card_img:hover{-webkit-filter: opacity(100%);filter: opacity(100%);}
.carusel_body{
    position: fixed;
    height: 100vh;
    width: 100vw;
    margin:auto;
    background-color: rgba(0, 0, 0, 0.411);
    top:0;
    left:0;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.carusel_element{
    object-fit: cover;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    max-height: 85%;
    max-width: 85%;
    cursor: pointer;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.35);
}
.carusel_title{
    background-color: black;
    color:blanchedalmond;
    position:fixed;
    bottom: 0;
    right: 0;
    width: 100vw;
    text-align: center;
    margin-bottom: 0;
    z-index: 1000;
    font-size:20pt;
}
.next_button{
    height: 100vh;
    position: absolute;
    top:0;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.671);
    cursor: pointer;
}

.next_button img, .previous_button img{
height: 20px;
margin: 15px;
}

.previous_button{
    height: 100vh;
    position: absolute;
    left:0;
    top:0;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.671);
    cursor: pointer;
}

.previous_button img{
    transform: rotate(180deg);
}
